import goodsHeader from './components/GoodsHeader.vue'
import popupSpec from '../../common/popupSpec.vue'
import common from '../../common/common.js'
var serverUrl = common.serverUrl

export default {
	components: {
		goodsHeader,
		popupSpec
	},
	data() {
			return {
				isKeep: false, // 收藏
				goodsData: {
					"swiperList": []
				},
				goodsInfo: {
						goods_id:"",
						name: "",
						price: "",
						number: 1,
						spec: ""
						},
				currentSwiper: 0, // 轮播图下标
				isKeep:false,
				userId:"",
				userInfo:{},
				token:'',
				type:'',
		}
		},
	onShow(){
		var userInfo = uni.getStorageSync("globalUser")
		if(userInfo!=null&&userInfo!=""&&userInfo!=undefined){
			this.userInfo=userInfo
			this.token="Bearer "+this.userInfo.token
			this.type=this.userInfo.tokenType
			this.userId=this.userInfo.userId
		}
	},
	onLoad(option) {
		var userInfo = uni.getStorageSync("globalUser")
		if(userInfo!=null&&userInfo!=""&&userInfo!=undefined){
			this.userInfo=userInfo
			this.token="Bearer "+this.userInfo.token
			this.type=this.userInfo.tokenType
			this.userId=this.userInfo.userId
		}
		this.goodsInfo = JSON.parse(option.goodsInfo);
		// 请求详情·
		// this.initData();
		uni.request({
			url:serverUrl+'/carousels/'+this.goodsInfo.goods_id,
			method:'GET',
			success: (res) => {
				if(res.data.code===0){
					this.goodsData.swiperList=res.data.data
				}
			}
		})
		
		
		uni.request({
			url:serverUrl+'/collects/'+this.goodsInfo.goods_id+'?userId='+this.userId,
			method:'GET',
			success: (res) => {
				if(res.data.code===0){
					this.isKeep=res.data.data
				}
			}
		})
	},
	methods: {
		setSelectSpec(item){
			// 选择规格
			this.goodsInfo.spec = item;
		},
		hideSpec(){
			this.spaceInfo.showSpace = false;
		},
		add(){
			this.goodsInfo.number++;
		},
		sub(){
			if(this.goodsInfo.number <= 1){
				return;
			}
			this.goodsInfo.number--;
		},
		
		//请求详情
		// initData() {
		// 	this.request({
		// 		url: interfaces.getGoods,
		// 		success: ((res) => {
		// 			this.goodsData = res.data;
		// 			// console.log(this.goodsData);
		// 		})
		// 	})
		// },
		swiperChange(event) {
			// console.log(event.detail.current);
			this.currentSwiper = event.detail.current;
		},
		handleRatings() {
			// 本地存储
			uni.setStorageSync('comments', this.goodsData.comment)
			// 页面跳转
			uni.navigateTo({
				url: "./ratings"
			})
		},
		keep(){
			uni.request({
				url:serverUrl+'/collects/?userId='+this.userId+'&apiRootId='+this.goodsInfo.goods_id,
				method:'POST',
				success: (res) => {
					if(res.data.code===0){
						this.isKeep=!this.isKeep
						uni.showToast({
							title:res.data.data,
							duration:1500
						})
					}
				}
			})
		},
		joinCart(){
			console.log(this.goodsInfo);
			
			// 存储到本地存储里
			
			// 1.先去本地存储中取
			uni.getStorage({
				key:"goodsList",
				success:(res => {
					// 拿数据
					let goodsList = res.data;
					// 查找商品是否存在
					let isExist = false;
					
					goodsList.forEach(goods => {
						if(goods.goods_id == this.goodsInfo.goods_id){
							// 如果存在 修改商品商量
							goods.number += this.goodsInfo.number;
							isExist = true;
						}
					})
					
					if(!isExist){
						goodsList.push(this.goodsInfo);
						// 更新本地存储
						this.setGoodsList(goodsList);
					}
					
					
				}),
				fail: (err => { // 没有得到数据,那么就存
					// console.log("加入失败")
					let goodsList = [];
					goodsList.push(this.goodsInfo);
					
					// 往本地存储中存储数据
					this.setGoodsList(goodsList);
				})
			})
		},
		setGoodsList(goodsList){
			// console.log("存储到本地存储中")
			// 存储到本地存储中
			uni.setStorage({
				key:"goodsList",
				data: goodsList,
				success:function(){
					uni.showToast({
						icon:"success",
						title:"添加购物车成功"
					})
				}
			})
		},
		handleBuy(){
			let tempList = [];
			tempList.push(this.goodsInfo);
			uni.setStorage({
				key:"confirmList",
				data: tempList,
				success: () => {
					uni.navigateTo({
						url:"../order/confirm"
					})
				}
			})
		}
	}
}
